import React from 'react'

export default function OldNextQuestion({ questionsList, selector }) {
  const OldNextQuestion = questionsList.map(question => (
    <div key={question.id}>
      {question.id === selector + 2 ? (
        <div>
          {' '}
          {question.id}|{question.text}
        </div>
      ) : null}
    </div>
  ))
  return <div>{OldNextQuestion}</div>
}
