import React from 'react'
import StatusPrevList from '../QuestionStatus/statusPrevList'
import { Col } from 'reactstrap'
export default function PrevQuestion({ questionsList, selector, status }) {
  const PrevQuestion = questionsList.map(question => (
    <div key={question.id}>
      {question.id === selector - 1 ? (
        <div>
          {' '}
          <Col md="auto">
            <div className="PrevQuestion">
              {question.id}|{question.text}
            </div>
          </Col>
          <Col md="auto">
            <StatusPrevList
              status={status}
              selector={selector}
            ></StatusPrevList>
          </Col>
        </div>
      ) : null}
    </div>
  ))
  return <div>{PrevQuestion}</div>
}
