import React, { Fragment } from 'react'

import { Switch, Route, Redirect } from 'react-router-dom'

import { Container } from 'reactstrap'

import Navigation from 'containers/student/Navigation'
import Footer from 'components/Footer'

import Students from '../Students'

import NotFound from 'components/NotFound'

export default function SchoolCategoryLayout() {
  return (
    <Fragment>
      <Navigation />
      <Container fluid className="content mb-3 d-flex flex-column">
        <Switch>
          <Redirect from="/:category/" exact to="/:category/students" />
          <Route path="/:category/students" exact component={Students} />
          <Route component={NotFound} />
        </Switch>
      </Container>
      <Footer />
    </Fragment>
  )
}
