import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Spinner
} from 'reactstrap'
import { LinkContainer } from 'react-router-bootstrap'
import UserNav from './UserNav'
import { t } from 'i18n'

import { anyFetching } from 'modules/fetching/selectors'
import { current as currentCategory } from 'modules/category/selectors'

class NavigationComponent extends Component {
  static propTypes = {
    anyFetching: PropTypes.bool.isRequired,
    category: PropTypes.string
  }
  constructor(props) {
    super(props)

    this.toggle = this.toggle.bind(this)
    this.state = {
      isOpen: false
    }
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    })
  }
  render() {
    const { anyFetching, category } = this.props
    return (
      <Navbar color="primary" dark expand="md" className="mb-3">
        <LinkContainer to={`/${category}`}>
          <NavbarBrand href={`/${category}`}>
            Quiz{anyFetching ? <Spinner size="sm" className="ml-1" /> : null}
          </NavbarBrand>
        </LinkContainer>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <LinkContainer to={`/${category}/stats`}>
                <NavLink href={`/${category}/stats`}>
                  {t('navigation.stats')}
                </NavLink>
              </LinkContainer>
            </NavItem>
            <UserNav />
          </Nav>
        </Collapse>
      </Navbar>
    )
  }
}

export default connect(state => ({
  anyFetching: anyFetching(state),
  category: currentCategory(state)
}))(NavigationComponent)
