import React from 'react'

export default function QuestionsList({ questionsList, selector }) {
  const QuestionsList = questionsList.map(question => (
    <div key={question.id}>
      {question.id === selector ? (
        <div>
          {question.id}|{question.text}
        </div>
      ) : null}
    </div>
  ))
  return <div className="Question-List">{QuestionsList}</div>
}
