import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSchool, faUserGraduate } from '@fortawesome/free-solid-svg-icons'
import { t } from 'i18n'

import { logoutAndRedirect } from 'modules/auth/actions'
import { user } from 'modules/auth/selectors'
import { USER_TYPE_STUDENT, USER_TYPE_SCHOOL } from 'modules/auth/constants'

const USER_TYPE_ICON = {
  [USER_TYPE_STUDENT]: faUserGraduate,
  [USER_TYPE_SCHOOL]: faSchool
}

function NavigationComponent({ user, logoutAndRedirect }) {
  return user ? (
    <UncontrolledDropdown nav inNavbar>
      <DropdownToggle nav caret>
        <FontAwesomeIcon icon={USER_TYPE_ICON[user.type]} /> {user.name}{' '}
        {user.surname}
      </DropdownToggle>
      <DropdownMenu right>
        <DropdownItem onClick={logoutAndRedirect}>{t('logout')}</DropdownItem>
      </DropdownMenu>
    </UncontrolledDropdown>
  ) : (
    <NavItem>
      <LinkContainer to="/login">
        <NavLink href="/login">{t('login')}</NavLink>
      </LinkContainer>
    </NavItem>
  )
}

NavigationComponent.propTypes = {
  user: PropTypes.object,
  logoutAndRedirect: PropTypes.func.isRequired
}

export default connect(
  state => ({
    user: user(state)
  }),
  { logoutAndRedirect }
)(NavigationComponent)
