import React from 'react'

export default function QuestionsListImage({ questionsList, selector }) {
  const QuestionsListImage = questionsList.map(question => (
    <div key={question.id}>
      {question.id === selector ? (
        <img
          className="Image-Container"
          src={`/img/${question.image}.jpg`}
          alt="No Instructions"
        />
      ) : null}
    </div>
  ))
  return <div>{QuestionsListImage}</div>
}
