import React from 'react'

export default function NextQuestion({ questionsList, selector }) {
  const NextQuestion = questionsList.map(question => (
    <div key={question.id}>
      {question.id === selector + 1 ? (
        <div>
          <div>
            {' '}
            {question.id}|{question.text}
          </div>
        </div>
      ) : null}
    </div>
  ))
  return <div>{NextQuestion}</div>
}
