import React from 'react'

export default function statusNextOldList({ selector, status }) {
  const statusNextOldList = status.map(item => (
    <div key={item.id}>
      {item.id === selector + 2 ? (
        <div className="CommonStatusList">
          <div className={item.text === 'true' ? 'true-selected' : 'true'} />
          <div className={item.text === 'false' ? 'false-selected' : 'false'} />
        </div>
      ) : null}
    </div>
  ))
  return (
    <div>
      <div className="CommonStatusList">
        <div className="true" />
        <div className="false" />
      </div>
      {statusNextOldList}
    </div>
  )
}
