import { CALL_API } from 'constants/api'
import { API_URL } from 'constants/endpoints'
import { createActionWithPayloadCreator } from 'utils/actionCreators'

import {
  FETCH_STUDENT_STATS,
  FETCH_STUDENT_STATS_SUCCESS,
  FETCH_STUDENT_STATS_FAILURE,
  FETCH_ALL_EXAM_STATS,
  FETCH_ALL_EXAM_STATS_SUCCESS,
  FETCH_ALL_EXAM_STATS_FAILURE,
  SET_PERIOD
} from './constants'

export const setPeriod = createActionWithPayloadCreator(SET_PERIOD)

export function fetchStudentStats() {
  return {
    [CALL_API]: {
      endpoint: `${API_URL}/v1/student`,
      method: 'GET',
      credentials: 'include',
      types: [
        FETCH_STUDENT_STATS,
        FETCH_STUDENT_STATS_SUCCESS,
        FETCH_STUDENT_STATS_FAILURE
      ]
    }
  }
}

export function fetchAllExamStats() {
  return {
    [CALL_API]: {
      endpoint: `${API_URL}/v1/student/me/exam`,
      method: 'GET',
      credentials: 'include',
      types: [
        FETCH_ALL_EXAM_STATS,
        FETCH_ALL_EXAM_STATS_SUCCESS,
        FETCH_ALL_EXAM_STATS_FAILURE
      ]
    }
  }
}
