import React from 'react'
import { Col } from 'reactstrap'
import StatusOldPrevList from '../QuestionStatus/statusOldPrevList'
export default function OldPrevQuestion({ questionsList, selector, status }) {
  const OldPrevQuestion = questionsList.map(question => (
    <div key={question.id}>
      {question.id === selector - 2 ? (
        <div>
          {' '}
          <Col md="auto">
            <div className="OldPrevQuestion">
              {question.id}|{question.text}
            </div>
          </Col>
          <Col md="auto">
            <StatusOldPrevList
              status={status}
              selector={selector}
            ></StatusOldPrevList>
          </Col>
        </div>
      ) : null}
    </div>
  ))
  return <div>{OldPrevQuestion}</div>
}
