import React from 'react'

export default function statusQuestionList({
  status,
  selector,
  handleAnswerTrueClick,
  handleAnswerFalseClick,
  handleNext
}) {
  const statusQuestionList = status.map(item => (
    <div key={item.id}>
      {item.id === selector ? (
        <div className="statusQuestionList">
          <div className="exception-for-question-list">
            <div>
              <div
                className={item.text === 'true' ? 'true-selected' : null}
                style={{
                  zIndex: '1'
                }}
              />
              <div
                className={item.text === 'false' ? 'false-selected' : null}
                style={{
                  zIndex: '1'
                }}
              />
              <div
                onMouseUp={handleNext}
                className={
                  item.text !== '' ? 'true-false-disable' : 'true-false-enable'
                }
              ></div>
            </div>
          </div>
        </div>
      ) : null}
    </div>
  ))
  return (
    <div>
      <div className="statusQuestionList">
        <div className="exception-for-question-list">
          <div
            onMouseDown={handleAnswerTrueClick}
            onMouseUp={handleNext}
            className="true"
          />
          <div
            onMouseDown={handleAnswerFalseClick}
            onMouseUp={handleNext}
            className="false"
          />
        </div>
      </div>
      <div>{statusQuestionList}</div>
    </div>
  )
}
