import React from 'react'
import PropTypes from 'prop-types'
import { Breadcrumb } from 'reactstrap'
import BreadcrumbRouterItem from 'components/breadcrumb/RouterItem'
import { t } from 'i18n'

export default function StatsBreadcrumbsComponent({ topic, exam }) {
  return (
    <Breadcrumb>
      <BreadcrumbRouterItem path="/:category/stats" activeExact>
        {t('breadcrumbs.statistics')}
      </BreadcrumbRouterItem>
      <BreadcrumbRouterItem path="/:category/stats/topic/:id" exact>
        {topic ? topic.text : '...'}
      </BreadcrumbRouterItem>
      <BreadcrumbRouterItem path="/:category/stats/exams">
        {t('breadcrumbs.exams')}
      </BreadcrumbRouterItem>
      <BreadcrumbRouterItem path="/:category/stats/exams/:id" exact>
        {exam ? exam.id : '...'}
      </BreadcrumbRouterItem>
    </Breadcrumb>
  )
}

StatsBreadcrumbsComponent.propTypes = {
  topic: PropTypes.object,
  exam: PropTypes.object
}
