import React, { Fragment, Component } from 'react'
import PropTypes from 'prop-types'
import { TransitionGroup, CSSTransition } from 'react-transition-group'
import QuestionsListImage from './QuestionComponents/QuestionsListImage'
import OldPrevQuestion from './QuestionComponents/OldPrevQuestion'
import PrevQuestion from './QuestionComponents/PrevQuestion'
import QuestionsList from './QuestionComponents/QuestionsList'
import StatusQuestionList from './QuestionStatus/statusQuestionList'
import NextQuestion from './QuestionComponents/NextQuestion'
import StatusNextList from './QuestionStatus/statusNextList'
import OldNextQuestion from './QuestionComponents/OldNextQuestion'
import StatusNextOldList from './QuestionStatus/statusNextOldList'
import NextPrevClick from './QuestionComponents/NextPrevClick'
import { Container, Row, Col } from 'reactstrap'
import './style.scss'

let colorTest = [
  '',
  'LightSlateGray ',
  'LimeGreen ',
  'DarkOrchid ',
  'Gold ',
  'Salmon ',
  'RoyalBlue ',
  'Sienna '
]

class QuizMenu extends Component {
  static propTypes = {
    areas: PropTypes.array.isRequired,
    category: PropTypes.string
  }

  constructor(props) {
    super(props)

    this.state = {
      index: 1,
      display: false,
      visibility: false,
      selector: 1,
      toipicId: 0,
      questionListPanel: false,
      answerTrue: false,
      answerFalse: false,
      title: '',
      warning: ''
    }
  }

  handleAnswerFalseClick() {
    this.setState({
      answerFalse: true
    })
    this.props.status.length === 41 - 1
      ? this.setState({ warning: 'stop here' })
      : this.props.updateStatus(this.state.selector, 'false')
  }
  handleAnswerTrueClick() {
    this.setState({
      answerTrue: true
    })
    this.props.status.length === 41 - 1
      ? this.setState({ warning: 'stop here' })
      : this.props.updateStatus(this.state.selector, 'true')
  }

  renderTopic = (topic, index) =>
    topic ? (
      <div
        className="underline"
        key={index}
        onMouseUp={this.handleOnclickFetch}
        onMouseDown={() =>
          this.setState({
            toipicId: topic.id,
            title: topic.text,

            questionListPanel: true
          })
        }
      >
        {topic.text}
      </div>
    ) : null
  handleNext() {
    this.state.selector === 40
      ? this.setState({ warning: 'stop here' })
      : this.setState({
          selector: this.state.selector + 1
        })
  }
  handlePrev() {
    this.state.selector === 1
      ? this.setState({ warning: 'stop here' })
      : this.setState({
          selector: this.state.selector - 1
        })
  }
  handleClick(index) {
    this.setState({ index: index, display: true, visibility: true })
  }
  handleOnclickFetch = () => {
    this.props.fetchquestionsList(this.state.toipicId)
    this.props.refreshStatus()
  }
  handlePanelClose() {
    this.props.refreshStatus()
    this.setState({
      questionListPanel: false,
      selector: 1,
      answerTrue: false,
      answerFalse: false,
      start: false,
      title: ''
    })
  }
  render() {
    const { areas, category, questionsList } = this.props

    let k = 1
    questionsList.map(n => {
      return (n['id'] = k++)
    })

    const Areas = areas.map(area => (
      <div key={area.id}>
        <div
          style={{
            backgroundColor: colorTest[this.state.index],
            position: 'relative',
            width: '1200px',
            overflow: 'hidden'
          }}
        >
          <div
            className="quiz-layout-catagory-container"
            style={{
              backgroundColor: colorTest[area.id]
            }}
            onClick={this.handleClick.bind(this, area.id)}
          >
            {area.text}
          </div>
        </div>
      </div>
    ))

    const Questions = areas.map(area => (
      <div key={area.id}>
        {this.state.index === area.id
          ? area.topics.map(this.renderTopic)
          : null}
      </div>
    ))

    return (
      <Fragment>
        <Container>
          <Row>
            <div>
              Category: <span className="text-uppercase">{category}</span>
            </div>
          </Row>
          <Row>
            <div className="quiz-layout-container">
              <Row>
                <Col md="auto">{Areas}</Col>
                <Col md="2"></Col>
              </Row>

              <TransitionGroup>
                <CSSTransition
                  in={true}
                  appear={false}
                  key={this.state.index}
                  timeout={{
                    exit: 5000
                  }}
                  classNames="slide"
                >
                  <div
                    className="questions"
                    style={{
                      backgroundColor: colorTest[this.state.index]
                    }}
                  >
                    <div className="quiz-layout-qustion-container">
                      <Row>
                        <Col md="auto">{Questions}</Col>
                        <Col md="2"></Col>
                      </Row>
                    </div>
                  </div>
                </CSSTransition>
              </TransitionGroup>
            </div>
          </Row>
          <div
            className={
              this.state.questionListPanel
                ? 'question-list-container-show'
                : 'question-list-container-hide'
            }
            style={{
              zIndex: '100'
            }}
          >
            <Row>
              <Row>
                <Col md="2">
                  <button
                    className="btn-close "
                    onClick={this.handlePanelClose.bind(this)}
                  >
                    X
                  </button>
                </Col>

                <div className="title">
                  <Col md="8">
                    {this.state.title} <hr></hr>
                  </Col>
                </div>

                <Col md="2"></Col>
              </Row>

              <Row>
                {' '}
                <div className="QuestionsListImage-container">
                  <Col md="auto">
                    <QuestionsListImage
                      questionsList={this.props.questionsList}
                      selector={this.state.selector}
                    ></QuestionsListImage>
                  </Col>
                  <Col md="8"></Col>
                </div>
              </Row>
              <Row>
                <div className="main-container">
                  <Row>
                    <div className="OldPrevQuestion-container">
                      <OldPrevQuestion
                        questionsList={this.props.questionsList}
                        selector={this.state.selector}
                        status={this.props.status}
                      ></OldPrevQuestion>
                    </div>{' '}
                  </Row>
                  <Row>
                    <div className="PrevQuestion-container">
                      <PrevQuestion
                        questionsList={this.props.questionsList}
                        selector={this.state.selector}
                        status={this.props.status}
                      ></PrevQuestion>
                    </div>
                  </Row>
                  <Row>
                    <div className="Question-sub-container">
                      <Col md="8">
                        <div className="QuestionsList-container ">
                          <QuestionsList
                            questionsList={this.props.questionsList}
                            selector={this.state.selector}
                          ></QuestionsList>
                        </div>
                      </Col>
                      <Col md="auto">
                        <StatusQuestionList
                          selector={this.state.selector}
                          status={this.props.status}
                          handleAnswerFalseClick={this.handleAnswerFalseClick.bind(
                            this
                          )}
                          handleAnswerTrueClick={this.handleAnswerTrueClick.bind(
                            this
                          )}
                          handleNext={this.handleNext.bind(this)}
                        ></StatusQuestionList>
                      </Col>
                    </div>
                  </Row>

                  <Row>
                    <div className="NextQuestion-sub-container">
                      <Col md="8">
                        <div className="NextQuestion-container">
                          <NextQuestion
                            questionsList={this.props.questionsList}
                            selector={this.state.selector}
                          ></NextQuestion>
                        </div>
                      </Col>
                      <Col md="auto">
                        <StatusNextList
                          selector={this.state.selector}
                          status={this.props.status}
                        ></StatusNextList>
                      </Col>
                    </div>
                  </Row>
                  <Row>
                    <div className="OldNextQuestion-sub-container">
                      <Col md="8">
                        <div className="OldNextQuestion-container">
                          <OldNextQuestion
                            questionsList={this.props.questionsList}
                            selector={this.state.selector}
                          ></OldNextQuestion>
                        </div>
                      </Col>
                      <Col md="auto">
                        <StatusNextOldList
                          selector={this.state.selector}
                          status={this.props.status}
                        ></StatusNextOldList>
                      </Col>
                    </div>
                  </Row>
                  <Row>
                    <Col md="auto">
                      {' '}
                      <NextPrevClick
                        handleNext={this.handleNext.bind(this)}
                        handlePrev={this.handlePrev.bind(this)}
                      ></NextPrevClick>
                    </Col>
                    <Col md="2"></Col>
                    <Col md="2"></Col>
                  </Row>
                </div>
              </Row>
            </Row>
          </div>
        </Container>
      </Fragment>
    )
  }
}

export default QuizMenu
